/* Importo tipografias */
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display+SC:ital,wght@0,400;0,700;1,900&display=swap");

/* Importo iconografia */
@import url("https://unpkg.com/boxicons@2.0.7/css/boxicons.min.css");

.body {
  align-items: center;

  background: var(--clr-bg);
}

.container {
  width: 60%;
}

legend {
  font-size: 1.7rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  color: var(--clr-typografyblack);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.section-container {
  width: 60%;
  margin: 0 auto;
  font-family: "Work Sans", sans-serif;
  padding-top: 2rem;
  padding-bottom: 2rem;

  text-align: justify;
}

.section-container ul li {
  display: inline-block;
  position: auto;
}

.section-container h1 {
  font-size: 2.5rem;
  color: var(--clr-typografyblack);
  margin-bottom: 20px;
}

.section-container h2 {
  font-size: 1.9rem;
  color: var(--clr-typografyblack2);
  margin-bottom: 20px;
  /* text-decoration: line-through; */
}

.section-container p {
  font-size: 1.6rem;
  color: var(--clr-typografyblack);
}

.section-container ul li i {
  font-size: 9em;
  margin: 0 2rem;
}

/* ========== Coloreo los iconos ========== */
.html5 {
  background: -webkit-linear-gradient(#ff5e00, #af4000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.css3 {
  background: -webkit-linear-gradient(#0000ff, #000088);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.js {
  color: yellow;
}

.react {
  color: #7ddfff;
}

.cpp {
  background: -webkit-linear-gradient(#00549d, #0000ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

