.container {
  width: 60%;
  margin: auto auto;
}

header {
  background: var(--clr-mayablue);
}

header::after {
  content: "";
  display: table;
  clear: both;
}

/* Shutter In Horizontal */
.logo-container {
  font-family: "MuseoModerno", cursive;
  font-size: 35px;
  padding: 10px 0;

  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  background: var(--clr-typografyh2);
  transition-property: color;
  transition-duration: 0.3s;
}

.logo-container:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--clr-mayablue);
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: 50%;
  transform-origin: 50%;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.logo-container:hover,
.logo-container:focus,
.logo-container:active {
  color: white;
}

.logo-container:hover:before,
.logo-container:focus:before,
.logo-container:active:before {
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
}

/* Fin del logo-container */

nav {
  float: right;
}

a {
  text-decoration: none;
  color: var(--clr-typografyblack);
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  display: inline-block;
  margin-left: 6rem;
  padding-top: 2em;
  position: relative;
}

nav a {
  text-transform: uppercase;
  font-size: 16px;
}

nav a:hover {
  color: var(--clr-typografyh1);
}

nav a::before {
  content: "";
  display: block;
  height: 1em;
  background-color: var(--clr-typografyh2);
  position: absolute;
  top: 0;
  width: 0%;
  transition: all ease-in-out 250ms;
}

nav a:hover::before {
  width: 100%;
}

